@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap");
* {
  margin: 0;
  padding: 0;
}
.container {
  width: 100%;
  display: flex;
  justify-content: center;
}

/* Navigation CSS ///////////////////////////////////// */

.logo-img {
  width: 35px;
  height: 38px;
}

.navbar {
  border-bottom: 1px solid black;
  position: fixed;
  z-index: 100;
}
.navbar-inner {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  margin-top: 1rem;
  margin-bottom: 1.4rem;
}

.nav-list {
  display: flex;
  justify-content: right;
  padding-right: 2rem;
  list-style: none;
}

.nav-item {
  padding-right: 2rem;
  font-weight: 600;
  font-family: "Source Sans Pro", sans-serif;
}

.nav-item:hover {
  color: darkgray;
  cursor: pointer;
}

.nav-item:active {
  color: #222;
}

/* Media Queries for Navigation ////////////////////// */

@media screen and (max-width: 767px) {
  .logo-img {
    width: 25px;
    height: 25px;
    padding-top: 5px;
  }

  .navbar {
    border-bottom: 1px solid black;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
  }
  .navbar-inner {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
  }

  .nav-list {
    display: flex;
    justify-content: right;
    padding-right: 0.5rem;
    list-style: none;
  }

  .nav-item {
    padding-right: 1rem;
    font-weight: 600;
    font-size: 0.75rem;
    font-family: "Source Sans Pro", sans-serif;
  }

  .nav-item:hover {
    color: darkgray;
    cursor: pointer;
  }

  .nav-item:active {
    color: #222;
  }
}

/* END Navigation CSS /////////////////////////////////// */

/* Start of Hero CSS //////////////////////////////////// */

.hero {
  width: 100%;
  background-color: #222;
  min-height: 425px;
  background-image: linear-gradient(
      to bottom,
      rgba(17, 17, 17, 0.9) 60%,
      rgba(17, 17, 17, 0.9) 60%
    ),
    url("../assets/banner-1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.hero-content {
  display: flex;
  justify-content: space-around;
  max-width: 69%;
}

.hero-left {
  max-width: 25%;
  padding-top: 5rem;
}

.hero-name {
  font-family: "Source Sans Pro", sans-serif;
  color: #eaeaea;
  font-size: 2rem;
}

.hero-info {
  padding: 1.5rem 0;
  color: #eaeaea;
  font-size: 0.9rem;
  line-height: 1rem;
  font-weight: 400;
  font-family: "Source Sans Pro", sans-serif;
}
.yellow-btn {
  background-color: #f5d76e;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-family: "Source Sans Pro", sans-serif;
}

.yellow-btn:hover {
  background-color: #222;
  padding: 1rem 2rem;
  font-size: 1rem;
  color: #eaeaea;
  border: none;
  border: 1px solid #eaeaea;
  font-family: "Source Sans Pro", sans-serif;
}
@media screen and (max-width: 767px) {
  .hero {
    min-height: 225px;
  }

  .hero-name {
    font-family: "Source Sans Pro", sans-serif;
    color: #eaeaea;
    font-size: 1.5rem;
  }

  .hero-info {
    padding: 1.5rem 0;
    color: #eaeaea;
    font-size: 0.9rem;
    line-height: 1rem;
    font-weight: 400;
    font-family: "Source Sans Pro", sans-serif;
  }
  .hero-content {
    display: flex;
    justify-content: space-around;
    max-width: 75%;
  }
  .hero-left {
    max-width: 80%;
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}

/* End of HERO Styles ////////////////////////////////////// */

/* Start About Styles ////////////////////////////////////////// */

.about-container {
  max-width: 80%;
  display: block;
  margin-left: 25%;
}

.about {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  gap: 30px;
  margin-top: 5rem;
}

.blk-btn {
  background-color: #222;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-family: "Source Sans Pro", sans-serif;
  color: #eaeaea;
}

.blk-btn:hover {
  background-color: #f5d76e;
  padding: 1rem 2rem;
  font-size: 1rem;
  color: black;

  font-family: "Source Sans Pro", sans-serif;
}

.about-info {
  padding-top: 3rem;
  padding-bottom: 2rem;
  margin-right: 2rem;
  padding-right: 4rem;
}

.about-headline {
  text-transform: uppercase;
  font-family: "Source Sans Pro", sans-serif;
}

.about-caption {
  font-family: "Source Sans Pro", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  font-size: 0.75rem;
}

.about-details {
  margin-right: 3rem;
  padding-bottom: 1.5rem;
}

.aboutus-headline {
  text-transform: uppercase;
  font-family: "Source Sans Pro", sans-serif;
  letter-spacing: 0.1rem;
  padding-bottom: 0.5rem;
}

.aboutus-info {
  max-width: 80%;
}

/* About Us Media Queries */

@media screen and (max-width: 768px) {
  .about {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    gap: 30px;
    margin-top: 5rem;
  }
  .aboutus-info {
    max-width: 80%;
  }

  .about-info {
    padding-top: 3rem;
    padding-bottom: 2rem;
    margin-right: 0.2rem;
    padding-right: 4rem;
  }

  .blk-btn {
    background-color: #222;
    padding: 1rem 1.5rem;
    font-size: 0.75rem;
    font-family: "Source Sans Pro", sans-serif;
    color: #eaeaea;
  }

  .blk-btn:hover {
    background-color: #f5d76e;
    padding: 1rem 1.5rem;
    font-size: 0.75rem;
    color: black;
    border: none;

    font-family: "Source Sans Pro", sans-serif;
  }
}

/* Media Query for about us section 428px  */

@media screen and (max-width: 428px) {
  .about-container {
    max-width: 80%;
    display: block;
    margin-left: 20%;
  }
}
/* End of About us Styles */

/* Start of Partners section */

.partners-img {
  max-height: 35px;
  padding-right: 4rem;
}

.partners {
  background-color: #222;
  margin-top: 4rem;
}

.partners-inner {
  display: flex;
  justify-content: center;
  padding-top: 1rem;
}

.partners-shell {
  height: 60px;
}

/* MEdia queries for the partners section */

@media screen and (max-width: 768px) {
  .partners-img {
    max-height: 25px;
  }

  .partners-shell {
    height: 60px;
  }
}

/* Media queries partners 468px */

@media screen and (max-width: 468px) {
  .partners-img {
    max-height: 25px;
  }

  .partners-shell {
    height: 60px;
  }

  .partners-img {
    max-height: 25px;
    padding-right: 1rem;
  }
}

/* Services CSS */

.icon {
  color: black;
  max-width: 60px;
  padding-left: 2.2rem;
  padding-right: 0.6rem;
}
.icon2 {
  border-radius: 4px;
}

.service-card {
  display: grid;
  grid-template-columns: 1fr 3fr;
  width: 300px;
}

.services-headline {
  text-transform: uppercase;
  font-family: "Source Sans Pro", sans-serif;
  letter-spacing: 0.1rem;
  padding-bottom: 0.5rem;
}

.service-container {
  width: 100%;
}

.service-main {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  gap: 30px;
  margin: 5rem;
  margin-left: 20rem;
  max-width: 70%;
}

/* Media query for services section */

@media screen and (max-width: 768px) {
  .service-main {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    gap: 30px;
    margin: 5rem;
    padding-left: 1.5rem;
    margin-left: 2rem;
    max-width: 70%;
  }
}

@media screen and (max-width: 820px) {
  .service-main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    gap: 30px;
    margin: 5rem;
    margin-left: 2rem;
    max-width: 70%;
  }
}

/* Media Query for 428px */

@media and screen (max-width: 428px) {
  .service-main {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    gap: 30px;
    margin: 5rem;
    margin-left: 0rem;
    max-width: 70%;
    margin-right: 1rem;
  }
}

/* End of services section css */

/* Work section */

.works {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(1, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  background-color: whitesmoke;
}

/* .div1 {
  grid-area: 1 / 7 / 5 / 11;
  display: hidden;
}
.div2 {
  grid-area: 1 / 3 / 5 / 7;
  display: hidden;
} */
.grid-area-works-3 {
  grid-area: 2 / 9 / 3 / 10;
}
.grid-area-works-4 {
  grid-area: 3 / 9 / 4 / 10;
}
.grid-area-works-5 {
  grid-area: 3 / 8 / 4 / 9;
}
.grid-area-works-6 {
  grid-area: 2 / 8 / 3 / 9;
}
.grid-area-works-7 {
  grid-area: 2 / 4 / 4 / 6;
}

.works-img {
  max-width: 300px;
}

.works-info {
  padding-top: 3rem;
  padding-bottom: 2rem;
  margin-right: 0.5rem;
  padding-right: 1rem;
}

.pad-right {
  padding-right: 4px;
}

.works-container {
  margin-top: 3rem;

  padding: 4rem;
  background-color: whitesmoke;
}

/* Media queries for Works area */

@media screen and (max-width: 786px) {
  .works {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    background-color: whitesmoke;
  }
  .works-img {
    max-width: 300px;
  }
  .grid-area-works-3 {
    grid-area: 5;
  }
  .grid-area-works-4 {
    grid-area: 4;
  }
  .grid-area-works-5 {
    grid-area: 3;
  }
  .grid-area-works-6 {
    grid-area: 2;
  }
  .grid-area-works-7 {
    grid-area: 1;
    padding-bottom: 2rem;
  }
}

/* Media query works area 468px */

@media screen and (max-width: 428px) {
  .works-img {
    max-width: 250px;
  }
}

/* media query 820px works */

@media screen and (max-width: 820px) {
  .works-img {
    max-width: 250px;
  }

  .works {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    background-color: whitesmoke;
  }
  .works-img {
    max-width: 300px;
  }
  .grid-area-works-3 {
    grid-area: 3 / 2;
  }
  .grid-area-works-4 {
    grid-area: 2 / 1;
  }
  .grid-area-works-5 {
    grid-area: 3;
  }
  .grid-area-works-6 {
    grid-area: 2;
  }
  .grid-area-works-7 {
    grid-area: 1 / 1;
    padding-bottom: 2rem;
  }
}

/* End of Works Area */

/* Start of Yellow Bar Area */

.yellow-container {
  width: 100%;
  background-color: #f5d76e;
}

.yellow-bar {
  display: flex;
  justify-content: space-around;
  padding: 2rem, 5rem;
  margin: 0, 5rem;
}
.yellow-h2 {
  padding-top: 1rem;
}

.yellow-position {
  padding: 2rem 5rem;
  margin: 0 5rem;
}

/* Media queries for yellow bar area */

@media screen and (max-width: 768px) {
  .yellow-bar {
    display: block;
    padding: 2rem, 1rem;
    margin: 0, 5rem;
  }
  .yellow-position {
    display: block;
    /* padding: 2rem 5rem; */
    text-align: center;
    margin: 0 0.5rem;
    padding-bottom: 2rem;
    width: 70%;
  }
  .yellow-h2 {
    padding: 1rem;
  }
}

/* media query for yellow bar 428px */

@media screen and (max-width: 428px) {
  .yellow-bar {
    display: block;
    padding: 2rem, 2rem;
    margin: 0, 3rem;
  }
  .yellow-position {
    display: block;
    padding-bottom: 2rem;
    width: 60%;
    /* margin-right: 3rem; */
    padding-right: 3rem;
  }
  .yellow-h2 {
    padding: 1rem;
  }
}

/* End of Yellow Bar Area */

/* Start of Footer */

.footer {
  background-color: #222;
  padding: 5rem 0;
  min-height: 200px;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.div1-footer {
  grid-area: 1 / 3 / 2 / 6;
}
.div2-footer {
  grid-area: 1 / 7 / 2 / 8;
}
.div3-footer {
  grid-area: 1 / 8 / 2 / 9;
}
.div4-footer {
  grid-area: 1 / 9 / 2 / 10;
}
.div5-footer {
  grid-area: 1 / 10 / 2 / 11;
}

.footer-headline {
  text-transform: uppercase;
  font-family: "Source Sans Pro", sans-serif;
  color: whitesmoke;
  letter-spacing: 0.8rem;
  padding-bottom: 0.5rem;
}

.footer-caption {
  font-family: "Source Sans Pro", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  font-size: 0.75rem;
  color: whitesmoke;
}

.footer-link {
  color: #f5d76e;
  font-family: "Source Sans Pro", sans-serif;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.footer-link:hover {
  color: whitesmoke;
  font-family: "Source Sans Pro", sans-serif;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.footer-link-position {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.footer-label {
  text-transform: uppercase;
  font-family: "Source Sans Pro", sans-serif;
  color: whitesmoke;
  letter-spacing: 0.1rem;
  padding-bottom: 0.5rem;
}

.footer-item {
  color: whitesmoke;
  text-transform: lowercase;
  font-family: "Source Sans Pro", sans-serif;
  text-decoration: none;
  list-style: none;
  font-weight: 200;
}

/* media queries for Footer area */
@media screen and (max-width: 768px) {
  .footer-content {
    /* display: block; */
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 3fr, repeat(5, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    padding-left: 2rem;
  }

  .div1-footer {
    grid-area: 1;
    padding-top: 1rem;
    padding-bottom: 4rem;
  }
  .div2-footer {
    grid-area: 2;
    padding-bottom: 2rem;
  }
  .div3-footer {
    grid-area: 3;
    padding-bottom: 2rem;
  }
  .div4-footer {
    grid-area: 4;
    padding-bottom: 2rem;
  }
  .div5-footer {
    grid-area: 5;
    padding-bottom: 2rem;
  }

  .footer {
    background-color: #222;
    padding: 2rem 0;
  }
}
